.ItemDisplay {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 80vh;
}

.ItemDisplay__image {
  height: 100%;
  width: 500px;
  min-width: 350px;

  object-fit: cover;

  margin: 0 2rem;
}

.ItemDisplay__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;

  flex-grow: 1;

  height: 150px;
}

.ItemDisplay__name {
  font-size: 2.3rem;
  text-align: center;
}

.ItemDisplay__prices {
  display: flex;
  font-size: 1.4rem;
}

.ItemDisplay__current {
  margin-right: 10px;
}

.ItemDisplay__previous {
  color: grey;
  text-decoration: line-through;
}

.ItemDisplay__input {
  display: flex;
}

.Item__number {
  width: 2.5rem;
  height: 44.93px;
  text-align: center;

  margin-right: 10px;
}

.Item__add {
  background: none;
  background-color: black;
  color: white;

  outline: none;
  border: none;

  font-size: 1rem;

  padding: 0.8rem 1.3rem;

  cursor: pointer;

  transition: all 0.1s ease-out;
}

.Item__add:hover {
  background-color: rgb(226, 68, 68);
}

@media only screen and (max-width: 990px) {
  .ItemDisplay__image {
    width: 100%;

    margin-top: 2rem;
  }
}

@media only screen and (max-width: 550px) {
  .ItemDisplay__image {
    object-fit: contain;
    height: auto;

    margin: 0;
    margin-top: 2rem;
  }
}
